html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: #222222;
}

body {
  font-family: "Noto Serif JP", serif;
  line-height: 2;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Noto Serif JP", serif;
  color: #222222;
  line-height: 1.2;
  font-weight: 500;
}

p {
  margin: 0;
}
p + p {
  margin-top: 1em;
}

ul, dl, figure {
  margin: 0;
  padding: 0;
}

dt {
  font-weight: normal;
}

ul, li {
  list-style: none;
}

a {
  color: #222222;
  transition: 0.4s;
}
a:before, a:after {
  transition: 0.4s;
}
a:hover {
  color: #222222;
  text-decoration: none;
  opacity: 0.7;
}

em {
  font-style: normal;
  font-weight: normal;
  color: inherit;
}

input,
select,
textarea {
  outline: none;
  vertical-align: middle;
  font-size: 16px;
  font-size: 1rem;
}

input:not([type=checkbox]):not([type=radio]):not([type=file]):not([type=hidden]):not([type=submit]):not([type=reset]):not([type=button]):not([type=image]):not([type=range]),
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  padding: 0.4em 0.7em;
  border: 1px solid #D1D1D1;
  box-sizing: border-box;
  font-family: "Noto Serif JP", serif;
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  outline: none;
  width: 100%;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 2;
}

address {
  margin-bottom: 0;
}

