====================================== */
/* parts
--------------------------------- */
#header-top {
  height: 100px;
  background-color: #fff;
  display: flex;
}
@media (max-width: 959px) {
  #header-top {
    height: 56px;
    justify-content: space-between;
  }
}
#header-top .site_tit {
  height: 219px;
  width: 135px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  z-index: 1;
  position: relative;
  padding: 7px 20px;
  box-sizing: border-box;
  text-align: center;
  line-height: 1.5;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);
}
@media (min-width: 960px) {
  #header-top .site_tit {
    height: 370px;
    width: 180px;
  }
}
@media (min-width: 1200px) {
  #header-top .site_tit {
    padding: 12px 20px;
    height: 473px;
    width: 200px;
  }
}
@media (max-width: 1199px) {
  #header-top .site_tit img {
    width: 120px;
  }
}
@media (max-width: 959px) {
  #header-top .site_tit img {
    width: 96px;
  }
}
#header-top .site_tit .logo {
  margin: auto;
}
#header-top .site_tit .text {
  font-size: 10px;
  font-size: 0.625rem;
  transform: scale(0.8);
  width: 125%;
  transform-origin: left;
  text-align: center;
  margin: 0;
}
@media (min-width: 1200px) {
  #header-top .site_tit .text {
    font-size: 12px;
    font-size: 0.75rem;
  }
}
#header-top .gnav {
  width: 48%;
  background-color: #00524C;
  padding: 15px 25px;
  box-sizing: border-box;
  font-size: 14px;
  font-size: 0.875rem;
}
@media (min-width: 1200px) {
  #header-top .gnav {
    width: 50%;
    padding: 15px 50px;
    font-size: 16px;
    font-size: 1rem;
  }
}
#header-top .gnav > ul > li {
  flex: auto;
  height: 100%;
  position: relative;
}
#header-top .gnav > ul > li > a {
  width: 100%;
  font-weight: 500;
  color: #fff;
  display: block;
  text-align: center;
  line-height: 1.5;
  position: relative;
}
#header-top .gnav > ul > li > a:before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  border-bottom: 3px solid #fff;
  opacity: 0;
}
#header-top .gnav > ul > li > a:hover {
  opacity: 1;
}
#header-top .gnav > ul > li > a:hover:before {
  animation-name: hoverDropdown;
  animation-duration: 0.4s;
  opacity: 1;
}
@keyframes hoverDropdown {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
#header-top .uk-dropdown {
  background: rgba(255, 255, 255, 0.9);
  padding: 10px 12px;
  min-width: 163px;
  top: calc(100% + 20px) !important;
  left: 0% !important;
}
#header-top .uk-dropdown a {
  font-size: 14px;
  font-size: 0.875rem;
  padding: 3px 0;
  display: inline-flex;
}
#header-top .uk-dropdown a:after {
  font-family: "FontAwesome", FontAwesome;
  content: "\f105";
  color: #00524C;
  margin-left: 7px;
  font-size: 17px;
  font-size: 1.0625rem;
  line-height: 1;
  margin-top: 6px;
}
#header-top .column-02 {
  min-width: 296px;
}
#header-top .column-02 .uk-grid-margin {
  margin-top: 0;
}
#header-top .contact {
  padding: 15px 20px;
  box-sizing: border-box;
  width: calc(52% - 280px);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.5;
  font-size: 12px;
  font-size: 0.75rem;
}
@media (min-width: 1200px) {
  #header-top .contact {
    width: calc(50% - 300px);
    font-size: 15px;
    font-size: 0.9375rem;
  }
}
#header-top .contact span {
  color: #00524C;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 500;
}
@media (min-width: 1200px) {
  #header-top .contact span {
    font-size: 19px;
    font-size: 1.1875rem;
  }
}

#header-sroll {
  z-index: 999;
  transition: 0.7s ease;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
#header-sroll .group-flex {
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: brightness(115%) blur(3px);
  height: 80px;
  display: flex;
  align-items: center;
  padding-left: 30px;
  box-sizing: border-box;
}
@media (min-width: 1200px) {
