    padding-left: 15px;
    height: 56px;
    justify-content: space-between;
  }
}
#header-sroll .logo {
  margin-right: 30px;
}
@media (max-width: 1199px) {
  #header-sroll .logo img {
    height: 25px;
  }
}
#header-sroll .group {
  padding-right: 35px;
  line-height: 1.5;
  font-size: 14px;
  font-size: 0.875rem;
}
@media (min-width: 1200px) {
  #header-sroll .group {
    font-size: 16px;
    font-size: 1rem;
    padding-top: 5px;
  }
}
#header-sroll .group span {
  font-weight: 500;
  font-size: 15px;
  font-size: 0.9375rem;
  color: #00524C;
}
@media (min-width: 1200px) {
  #header-sroll .group span {
    font-size: 18px;
    font-size: 1.125rem;
  }
}
#header-sroll .group .uk-grid-margin {
  margin-top: 5px;
}
#header-sroll .gnav {
  margin-top: 12px;
  font-weight: 500;
}
@media (min-width: 1200px) {
  #header-sroll .gnav {
    margin-top: 22px;
  }
}
#header-sroll .gnav > ul > li {
  position: relative;
  flex: auto;
}
@media (min-width: 1200px) {
  #header-sroll .gnav > ul > li {
    margin-left: 10px;
  }
}
#header-sroll .gnav > ul > li > a {
  white-space: nowrap;
  display: block;
  box-sizing: border-box;
  padding: 0 30px;
  width: 100%;
  text-align: center;
  position: relative;
}
#header-sroll .gnav > ul > li > a:before {
  content: "";
  position: absolute;
  bottom: -11px;
  left: 0;
  width: 100%;
  border-bottom: 3px solid #00524C;
  opacity: 0;
}
#header-sroll .gnav > ul > li > a:hover {
  opacity: 1;
}
#header-sroll .gnav > ul > li > a:hover:before {
  animation-name: hoverDropdown;
  animation-duration: 0.4s;
  opacity: 1;
}
@keyframes hoverDropdown {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
#header-sroll .uk-dropdown {
  background: rgba(255, 255, 255, 0.9);
  padding: 7px 12px;
  min-width: 163px;
  top: calc(100% + 11px) !important;
  left: 0% !important;
}
#header-sroll .uk-dropdown a {
  font-size: 14px;
  font-size: 0.875rem;
  padding: 5px 0;
  display: inline-flex;
}
#header-sroll .uk-dropdown a:after {
  font-family: "FontAwesome", FontAwesome;
  content: "\f105";
  color: #00524C;
  margin-left: 7px;
  font-size: 17px;
  font-size: 1.0625rem;
