.form-range-01 {
  --dir: right;
  --fill: #B19968;
  --fillbg: rgba(100, 100, 100, 0.15);
  --star: url(../img/common/star.svg);
  --stars: 4;
  --starsize: 3rem;
  --symbol: var(--star);
  --value: 1;
  --w: calc(var(--stars) * var(--starsize));
  --x: calc(100% * (var(--value) / var(--stars)));
  display: flex;
}
.form-range-01::before {
  content: "";
  display: inline-block;
  width: var(--starsize);
  height: var(--starsize);
  background: var(--symbol) no-repeat center;
  background-size: 100%;
}
.form-range-01 input[type=range] {
  block-size: var(--starsize);
  inline-size: var(--w);
  position: relative;
  touch-action: manipulation;
  -webkit-appearance: none;
  display: block;
  margin: 0;
}
.form-range-01 input[type=range]::-moz-range-track {
  background: var(--fillbg);
  block-size: 100%;
  mask: repeat left center/var(--starsize) var(--symbol);
}
.form-range-01 input[type=range]::-moz-range-progress {
  background: var(--fill);
  block-size: 100%;
  mask: repeat left center/var(--starsize) var(--star);
}
.form-range-01 input[type=range]::-webkit-slider-runnable-track {
  background: var(--fillbg);
  block-size: 100%;
  mask: repeat left center/var(--starsize) var(--symbol);
  -webkit-mask: repeat left center/var(--starsize) var(--symbol);
}
.form-range-01 input[type=range]::-moz-range-thumb {
  height: var(--starsize);
  opacity: 0;
  width: var(--starsize);
}
.form-range-01 input[type=range]::-webkit-slider-thumb {
  background-color: var(--fill);
  height: var(--starsize);
  opacity: 1;
  width: 1px;
  -webkit-appearance: none;
  box-shadow: calc(0rem - var(--w)) 0 0 var(--w) var(--fill);
}

.wpcf7-radio,
.wpcf7-checkbox {
  margin-left: -2em;
}
.wpcf7-radio > *,
.wpcf7-checkbox > * {
  margin-left: 2em !important;
}

.wpcf7-acceptance > * {
  margin-left: 0 !important;
}

input[type=radio] {
  position: relative;
  vertical-align: middle;
  -webkit-appearance: none;
  border: 1px solid #cccccc;
  background-position: 50% 50%;
  width: 18px;
  height: 18px;
  display: inline-block;
  border-radius: 50%;
  outline: none;
  margin: 0 5px 0 0;
}
input[type=radio]:checked {
  border-color: #00524C;
  border-width: 2px;
}
input[type=radio]:checked:after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  background-color: #00524C;
  border-radius: 50%;
}
input[type=radio] label {
  display: inline-block;
}

input[type=checkbox] {
  margin: -4px 6px 0 0;
  width: 18px;
  height: 18px;
  border: 1px solid #686868;
  vertical-align: middle;
  -webkit-appearance: none;
  display: inline-block;
  outline: none;
  border-radius: 2px;
}
input[type=checkbox]:checked {
  position: relative;
  background-color: #686868;
}
input[type=checkbox]:checked:after {
  content: "";
  display: block;
  width: 8px;
  height: 13px;
  border: 2px solid #fff;
  box-sizing: border-box;
  border-top: none;
  border-left: 0;
  transform: rotate(45deg);
  position: absolute;
  top: -1px;
  left: 4px;
}
input[type=checkbox] label {
  display: inline-block;
}

